import React from 'react';
import { FormGroup, Label, Input, Button } from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.css';
import '../App.css';
import 'animate.css';
import QUESTIONS from '../questions/questions';
import { withRouter } from "react-router-dom";
import { LocalForm } from 'react-redux-form';
import emailjs from 'emailjs-com';

const questions = QUESTIONS;

class Survey extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      location: this.props.location,

    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  };

  handleChange(e) {
    this.setState({
      [e.target.id]: e.target.value,
    });
    console.log(JSON.stringify(this.state));
    JSON.stringify(this.state);
  }


  handleSubmit() {
    if (this.state.vaccine === "yes" || (this.state.Q0 && this.state.Q1)) {
      let answers = {
        "Location": this.state.location.state.store,
        "Are you currently vaccinated for covid-19?": this.state.vaccine,
        "Would you get a covid-19 vaccination to keep employment?": this.state.Q0,
        "Would you agree to weekly covid testing?": this.state.Q1,
        "Comments": this.state.comments,
        "Name": this.state.firstName + " " + this.state.lastName
      }

      console.log(answers)
      var templateParams = {
        location: this.state.location.state.store,
        name: this.state.firstName + " " + this.state.lastName,
        vaccinated: this.state.vaccine,
        wouldVax: this.state.Q0,
        wouldTest: this.state.Q0,
        comments: this.state.comments
      }
      emailjs.send('service_otasa49', 'template_qxdmg3b', templateParams, "user_TvtjIJKeXYtPG33fQF9jF")
        .then(function (response) {
          console.log('SUCCESS!', response.status, response.text);
        }, function (error) {
          console.log('FAILED...', error);
        })
      this.props.history.push('/finish');
    } else {
      alert("Please answer all questions!")
    }
  }


  render() {
    return (
      <div className="container p-5 animate__animated animate__fadeIn">
        <header>
          <img className=" logo2 mx-auto d-block animate__animated animate__pulse animate__infinite infinite" alt="wendysLogo" src="../images/wendy.png" />
        </header>
        <FormGroup className="text-center" >
          <Label required className="text-center form-group question">Are you currently vaccinated for covid-19?</Label>
          <FormGroup check >
            <Label check className="p-1" required >
              <Input type="radio" id="vaccine" name="vaccine" value="yes" className="mx-auto"
                onChange={this.handleChange}
              />
              Yes
            </Label>
            <Label check className="p-1">
              <Input type="radio" id="vaccine" name="vaccine" value="no" className="mx-auto"
                onChange={this.handleChange}
              />
              No
            </Label>
          </FormGroup>
          <hr className="display-3" />
        </FormGroup>
        <LocalForm name='survey' className="text-center" onSubmit={this.handleSubmit} >
          <VaccineInfo handleChange={this.handleChange} visible={this.state.vaccine === 'no'} />
          <FormGroup className="text-center" >
            <Label className="text-center form-group question">Name:</Label>
            <Input type="text" id="firstName" name="firstName" className="mx-auto contact" placeholder="First Name" required
              onChange={this.handleChange}
            />
            <Input type="text" id="lastName" name="lastName" className="mx-auto contact" placeholder="Last Name" required
              onChange={this.handleChange}
            />
          </FormGroup>
          <hr className="display-3" />
          <div class="text-center">
            <Button className="animate__animated animate__fadeIn" color="danger" type="submit" >Submit</Button>
          </div>
        </LocalForm>

      </div>

    )

  }

}

function VaccineInfo(props) {
  if (!props.visible) {
    return null;
  }

  return (
    <div>
      {questions.map(q => <div>
        <Label className="text-center form-group question">{q.question}</Label>
        <FormGroup check>
          <Label check className="p-1" >
            <Input type="radio" id={q.id} name={q.question} value="Yes" className="mx-auto"
              onChange={props.handleChange} />
            Yes
          </Label>
          <Label check className="p-1">
            <Input type="radio" id={q.id} name={q.question} value="No" className="mx-auto"
              onChange={props.handleChange}
            />
            No
          </Label>
        </FormGroup>
        <hr className="display-3" />
      </div>
      )}
      <Label className="text-center form-group question">Comments:</Label>
      <Input type="textarea" id="comments" name="comments" className="mx-auto" placeholder="Leave us a comment"
        onChange={props.handleChange}
      />
    </div>
  )
}



export default withRouter(Survey);